.emptyCart {
  margin: auto;
  text-align: center;
  padding: 10vmax;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.emptyCart>svg {
  font-size: 5vmax;
  color: rgb(97, 61, 41);
}

.emptyCart>p {
  font-size: 2vmax;
}

.emptyCart>a {
  background-color: rgb(51, 51, 51);
  color: white;
  border: none;
  padding: 1vmax 3vmax;
  cursor: pointer;
  font: 400 1vmax "Roboto";
  text-decoration: none;
}

.cartPage {
  padding: 10vmax;
}

.cartHeader {
  background-color: rgba(37, 29, 28, 0.866);
  width: 90%;
  box-sizing: border-box;
  margin: auto;
  color: white;
  display: grid;
  grid-template-columns: 4fr 1fr 1fr;
  font: 300 0.9vmax "Roboto";
}

.cartHeader>p {
  margin: 10px;
}

.cartHeader>p:last-child {
  text-align: end;
}

.cartContainer {
  width: 90%;
  margin: auto;
  display: grid;
  grid-template-columns: 4fr 1fr 1fr;
}

.cartInput {
  display: flex;
  align-items: center;
  height: 8vmax;
}

.cartInput>button {
  border: none;
  background-color: rgba(0, 0, 0, 0.616);
  padding: 0.5vmax;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
}

.cartInput>button:hover {
  background-color: rgba(0, 0, 0, 0.767);
}

.cartInput>input {
  border: none;
  padding: 1vmax;
  width: 3vmax;
  text-align: center;
  outline: none;

  font: 400 1vmax "Roboto";
  color: rgba(0, 0, 0, 0.74);
}

.cartSubtotal {
  display: flex;
  padding: 0.5vmax;
  height: 8vmax;
  align-items: center;
  box-sizing: border-box;
  font: 300 1vmax cursive;
  justify-content: flex-end;
  color: rgba(0, 0, 0, 0.753);
}

.cartGross {
  display: grid;
  grid-template-columns: 2fr 1.2fr;
}

.cartGrossBox {
  border-top: 3px solid rgb(97, 61, 41);;
  margin: 1vmax 4vmax;
  box-sizing: border-box;
  padding: 2vmax 0;
  font: 300 1vmax "Roboto";
  display: flex;
  justify-content: space-between;
}

.checkOutBtn {
  display: flex;
  justify-content: flex-end;
}

.checkOutBtn>button {
  background-color: rgb(97, 61, 41);;
  color: white;
  border: none;
  padding: 0.8vmax 4vmax;
  width: 50%;
  font: 300 0.8vmax "Roboto";
  margin: 1vmax 4vmax;
  cursor: pointer;
  border-radius: 30px;
}

@media screen and (max-width: 1000px ) {
  .cartPage {
    padding-top: 0;
    min-height: 60vh;
    /* height: 65vh; */
  }

  .cartHeader {
    margin-top: 7vh;
    width: 100%;
    font: 300 1.7vmax "Roboto";
    grid-template-columns: 3fr 1fr 1fr;
  }

  .cartContainer {
    width: 100%;
    height: 100%;

    grid-template-columns: 3fr 1fr 1fr;
    
  }

  .cartInput {
    /* width: 10vw; */
    height: 20vmax;
  }
  .cartInput > Button{
    width: 5vw;
  }

  .cartInput>button {
    padding: 1.5vmax;
  }

  .cartInput>input {
    width: 2vmax;
    padding: 1.5vmax;
    font: 400 1.8vmax "Roboto";
  }

  .cartSubtotal {
    padding: 1.5vmax;
    height: 20vmax;
    font: 300 2vmax "Roboto";
  }

  .cartGross {
    display: grid;
    grid-template-columns: 0fr 2fr;
  }

  .cartGrossBox {
    padding: 2vmax;
    font: 300 2vmax "Roboto";
  }

  .checkOutBtn>button {
    padding: 2vmax 4vmax;
    width: 100%;
    font: 300 2vmax "Roboto";
  }
}

@media screen and (max-width: 500px) {
  .cartContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgba(181, 180, 180, 0.17);
    height: 30vh;
  }
  .cartContainer:after{
    display: block;
    content:"";
    width: 80vw;
    background-color: white;
    height: 4vw;
  }
}